const Sub = (props: any) => {
  const { children, title }: { children: React.ReactNode; title: string } =
    props;

  return (
    <Box
      component='span'
      sx={{
        '&::before': {
          content: `"${title}"`,
          display: 'block',
          color: 'primary.main',
          mb: 0,
          top: 0,
          left: 0,
          fontSize: '1rem',
          letterSpacing: 2,
          lineHeight: 1
        }
      }}
    >
      {children}
    </Box>
  );
};

const num = (n: number): string => {
  const map: any = {
    1: '',
    2: '',
    3: '',
    4: '',
    5: ''
  };
  return map[n] || '';
};

const Point = ({
  title,
  description,
  index
}: {
  title: string;
  description: string;
  index: number;
}) => {
  return (
    <Box mb={2}>
      <Typography variant='h4' mr={1} p={0} color='primary' lineHeight={1.5}>
        {num(index + 1)}
        {title}
      </Typography>
      <Typography>{description}</Typography>
    </Box>
  );
};

